<template>
  <div >
    <!-- 切换tab -->
    <div id="tabs">
      <el-tabs type="card" @tab-click="handleClick" v-model="activeName">
        <el-tab-pane
          :label="val.title"
          :name="val.name"
          v-for="(val, index) in tabsList"
          v-bind:key="index"
        ></el-tab-pane>
      </el-tabs>
    </div>
    <!-- 查询 -->
    <div id="screen" >
      <div >
      <div class="screen_item" id="name">
        <label for>申诉服务类型：</label>
        <el-select v-model="serviceTypeId" placeholder="请选择">
          <el-option
            v-for="item in serviceTypeList"
            :key="item.id"
            :label="item.title"
            :value="item.id"
          ></el-option>
        </el-select>
      </div>
      <div class="screen_item" id="name">
        <label for>申诉服务对象：</label>
        <el-select v-model="select_type2" placeholder="请选择">
          <el-option
            v-for="item in serviceTypeList2"
            :key="item.title"
            :label="item.title"
            :value="item.title"
          ></el-option>
        </el-select>
      </div>
      <div class="screen_item" id="name">
        <label for>申诉服务状态：</label>
        <el-select v-model="order_status" placeholder="请选择">
          <el-option
            v-for="item in orderStatusList"
            :key="item.title"
            :label="item.title"
            :value="item.title"
          ></el-option>
        </el-select>
      </div>
      </div>
      <div style="margin-top:12px">
      <div class="screen_item" id="name">
        <label >订单号：</label>
        <el-input v-model="input_order" placeholder="输入订单号"  ></el-input>
      </div>
      <div class="screen_item" id="phone">
        <label for>申诉人手机号：</label>
        <el-input v-model="input_phone" placeholder="输入手机号" ></el-input>
      </div>
      </div>
      <div class="screen_item" id="buttons" style="margin-top:12px">
        <!-- <el-button @click="dialogTableVisible = true" type="text" size="small">点击查看</el-button> -->
        <el-button @click="querybtnClick" type="primary" style="width:98px;height:40px">查询</el-button>
        <el-button @click="cleanbtnClick">清除查询</el-button>
      </div>
      <!-- 数据统计 -->
    </div>
       <div id="table_header">
      <label for id="total" >{{'数据统计：共' + num + '条'}}</label>
      <!-- <el-button id="add" @click="addbtnClick" type="primary">添加参赛人员</el-button> -->
    </div>
    <!-- 表格 -->
        <div id="table" >
      <el-table :data="tableData" border>
        <el-table-column prop="mother_order" label="母订单号" align="center"></el-table-column>
        <el-table-column prop="child_order" label="子订单号" align="center"></el-table-column>
        <el-table-column prop="phone" label="投诉手机号码" align="center"></el-table-column>
        <el-table-column prop="service_type" label="服务类型" align="center"></el-table-column>
        <el-table-column prop="order_source" label="订单来源" align="center" width="120"></el-table-column>
         <el-table-column prop="complain_status" label="申诉状态" align="center"></el-table-column>
          <el-table-column prop="dispose_status" label="处理状态" align="center"></el-table-column>
        <el-table-column prop="hander" label="操作" align="center">
          <template slot-scope="scope">
            <el-button @click="jumpDetails(scope.row)" type="text" size="small">查看详情</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div id="table_bottom" >
  <el-pagination
    @current-change="handleCurrentChange"
    next-text="下一页"
    prev-text="上一页"
    layout="prev, pager, next"
    :total="total"
  ></el-pagination>
</div>
  </div>
</template>
<script>
import {getComplainList, getServiceTypeList} from '../../../service/complainService'
import { getLoginInfo } from '../../../assets/common/utils.js'
export default {
  name: 'manageComplain',
  data () {
    return {
      serviceTypeId: '',
      select_type2: '全部',
      order_status: '全部',
      activeName: '1',
      dealStatus: 'pending',
      serviceTypeList: [
      ],
      serviceTypeList2: [
        {
          title: '雇主'
        },
        {
          title: '家政公司'
        },
        {
          title: '阿姨'
        },
        {
          title: '全部'
        }
      ],
      orderStatusList: [
        {
          title: '全部'
        },
        {
          title: '进行中'
        },
        {
          title: '已结束'
        }
      ],
      input_order: '',
      input_phone: '',
      tabsList: [
        {
          title: '待解决',
          name: '1'
        },
        {
          title: '已解决',
          name: '2'
        }
      ],
      total: 0,
      page: 1,
      page_size: 10,
      tableData: [],
      num: 0,
      company_id: ''
    }
  },
  created () {
    // 面包屑
    this.$store.dispatch('commitChangeTopTitles', ['投诉管理', '订单申诉'])
  },
  mounted () {
    this.num = this.tableData.length
    // 获得公司id
    this.company_id = getLoginInfo('company_id')
    // 服务类型列表
    getServiceTypeList().then(res => {
      this.serviceTypeList = res.data.items
    })
    this.getComplainList()
  },
  methods: {
    // 切换tabs
    handleClick (tab, event) {
      console.log(tab, event)
      if (tab.label === '待解决') {
        this.dealStatus = 'pending'
      } else {
        this.dealStatus = 'done'
      }
      this.getComplainList()
    },
    // 点击切换页面
    handleCurrentChange (val) {
      this.page = val
      // this.getLeaderList()
    },
    // 点击查询
    querybtnClick () {
      console.log('querybtnClick')
      this.getLeaderList()
    },
    // 清除查询内容
    cleanbtnClick () {
      console.log('cleanbtnClick')
      this.name = ''
      this.input_order = ''
      this.input_phone = ''
      this.getLeaderList()
    },
    // 跳到详情页面
    jumpDetails (val) {
      this.$router.push('/complainDetails')
    },
    // 申诉列表
    getComplainList () {
      let params = `housework_company_id=${this.company_id}&page=${this.page}&per_page=${this.page_size}`
      if (this.input_phone) {
        params += `&user_phone=${this.input_phone}`
      }
      if (this.orderCode) {
        params += `&order_code=${this.input_order}`
      }
      if (this.serviceTypeId) {
        params += `&service_type_id=${this.serviceTypeId}`
      }
      if (this.orderStatus) {
        params += `&order_status=${this.order_status}`
      }
      if (this.dealStatus) {
        params += `&complaint_deal_status=${this.dealStatus}`
      }
      getComplainList(params)
        .then(res => {
        })
    }
  }
}
</script>
<style scoped>
.el-tabs--card >>> .el-tabs__header .el-tabs__item.is-active {
  border-bottom: #6a89cc 5px solid;
  color: #000000;
  font-size: 16px;
  font-weight: bolder;
}
#table_header {
  overflow: hidden;
}
#total {
  float: left;
  margin-top: 18px;
  padding: 0px 5px;
  color: white;
  background-color: #6a89cc;
  float: left;
  border-radius: 3px;
}
#tabs {
  margin-top: 12px;
}
#screen {
  text-align: left;
  overflow: hidden;
  margin: 10px auto;
}
.screen_item {
  width: 25%;
  padding-right: 20px;
  padding-bottom: 5px;
  box-sizing: border-box;
  display: inline-flex;
}
.screen_item label {
  line-height: 40px;
  font-weight: 700;
}
.screen_item .el-input {
  flex: 1;
}
#table {
  margin-top: 18px;
}
.el-table >>> th {
   background-color: #e3eefa;
    font-family: "\82F9\65B9-\7B80 \5E38\89C4\4F53", "\82F9\65B9-\7B80 \5E38\89C4\4F53", "\82F9\65B9-\7B80 \5E38\89C4\4F53";
    color: #000000;
    font-size: 16px;
    font-weight: bold;
     border:none;
}
.el-table >>> th .cell {
  font-family: "苹方-简 常规体", "苹方-简 常规体", "苹方-简 常规体";
  font-weight: bolder;
  color: #333;
}
.el-table >>> th:first-child .cell {
  border-left: transparent solid 1px;
  font-family: "苹方-简 常规体", "苹方-简 常规体", "苹方-简 常规体";
  font-weight: bolder;
  color: #333;
}
#table_bottom {
  margin-top: 10px;
}
</style>



